<template>
  <div class="region-chart">
    <h3 class="chart-title">üìà Âπ≥ÂùáËñ™ËµÑ Top10 ÂüéÂ∏Ç</h3>
    
    <div id="topSalaryBarChart" class="chart-container"></div>
  </div>
</template>

<script setup>
import { ref, onMounted, watch } from 'vue'
import * as echarts from 'echarts'

const props = defineProps({
  data: {
    type: Array,
    default: () => []
  }
})

const chartInstance = ref(null)

// ÂàùÂßãÂåñÂõæË°®
const initChart = () => {
  if (!props.data || props.data.length === 0) return
  
  // Á°Æ‰øùdomÂÖÉÁ¥†Â∑≤Âä†ËΩΩ
  const chartDom = document.getElementById('topSalaryBarChart')
  if (!chartDom) return
  
  chartInstance.value = echarts.init(chartDom)
  
  // Â§ÑÁêÜÂüéÂ∏ÇÂêçÁß∞ÂíåÊï∞ÊçÆ
  const cities = props.data.map(item => {
    // Â¶ÇÊûúÂüéÂ∏ÇÂêçÁß∞Â§™ÈïøÔºåÊà™ÂèñÂâç4‰∏™Â≠óÁ¨¶Âä†ÁúÅÁï•Âè∑
    const cityName = item.city && item.city.length > 4 ? 
      item.city.substring(0, 4) + '..' : 
      (item.city || 'Êú™Áü•')
    return cityName
  }).slice(0, 10) // Âè™ÂèñÂâç10‰∏™
  
  // Á°Æ‰øùËñ™ËµÑÊòØÊï∞Â≠óÁ±ªÂûã
  const salaries = props.data.map(item => {
    const salary = typeof item.avgSalary === 'number' ? 
      item.avgSalary : 
      (typeof item.avg_salary === 'number' ? 
        item.avg_salary : 
        parseFloat(item.avgSalary || item.avg_salary) || 0)
    return parseFloat(salary.toFixed(1))
  }).slice(0, 10)
  
  const option = {
    backgroundColor: 'transparent',
    tooltip: {
      trigger: 'axis',
      axisPointer: { type: 'shadow' },
      formatter: (params) => {
        const idx = params[0].dataIndex
        const item = props.data[idx]
        const city = item.city || 'Êú™Áü•'
        const salary = typeof item.avgSalary === 'number' ? 
          item.avgSalary : 
          (typeof item.avg_salary === 'number' ? 
            item.avg_salary : 
            parseFloat(item.avgSalary || item.avg_salary) || 0)
            
        return `<div style="font-weight:bold;margin-bottom:5px">${city}</div>
                <span style="display:inline-block;width:10px;height:10px;border-radius:50%;background:#66CCFF;"></span>
                <span style="margin-left:5px">Âπ≥ÂùáËñ™ËµÑ: ${salary.toFixed(1)}K</span>`
      },
      backgroundColor: 'rgba(13, 36, 81, 0.9)',
      borderColor: 'rgba(109, 198, 255, 0.3)',
      borderWidth: 1,
      textStyle: { color: '#fff' }
    },
    grid: {
      left: '3%',
      right: '4%',
      bottom: '3%',
      top: '3%',
      containLabel: true
    },
    xAxis: { 
      type: 'value', 
      name: 'Âπ≥ÂùáËñ™ËµÑ(K)', 
      nameTextStyle: { color: '#aaa', align: 'right', padding: [0, 10, 0, 0] },
      axisLabel: { 
        color: '#fff',
        formatter: '{value}K'
      },
      axisLine: {
        lineStyle: { color: 'rgba(255, 255, 255, 0.2)' }
      },
      splitLine: {
        lineStyle: { color: 'rgba(255, 255, 255, 0.1)' }
      }
    },
    yAxis: {
      type: 'category',
      data: cities,
      axisLabel: { 
        color: '#fff',
        fontSize: 11,
        margin: 10,
        formatter: (value) => {
          // Â§ÑÁêÜÁúÅÁï•ÂíåË∂ÖÈïøÂüéÂ∏ÇÂêç
          return value
        }
      },
      axisLine: {
        lineStyle: { color: 'rgba(255, 255, 255, 0.2)' }
      },
      axisTick: {
        alignWithLabel: true,
        lineStyle: { color: 'rgba(255, 255, 255, 0.2)' }
      }
    },
    series: [
      {
        name: 'Âπ≥ÂùáËñ™ËµÑ',
        type: 'bar',
        data: salaries,
        itemStyle: { 
          color: new echarts.graphic.LinearGradient(0, 0, 1, 0, [
            { offset: 0, color: '#0099FF' },
            { offset: 1, color: '#66FFFF' }
          ]),
          borderRadius: [0, 4, 4, 0]
        },
        label: {
          show: true,
          position: 'right',
          color: '#fff',
          formatter: '{c}K'
        },
        barWidth: '60%',
        animationDuration: 1500,
        animationEasing: 'elasticOut'
      }
    ]
  }

  chartInstance.value.setOption(option)
  
  // Ê∑ªÂä†Á™óÂè£Â§ßÂ∞èÂèòÂåñÁõëÂê¨
  window.addEventListener('resize', () => {
    if (chartInstance.value) {
      chartInstance.value.resize()
    }
  })
}

// ÂΩìÊï∞ÊçÆÂèòÂåñÊó∂ÈáçÊñ∞ÁªòÂà∂ÂõæË°®
watch(() => props.data, (newData) => {
  if (newData && newData.length > 0) {
    // Â¶ÇÊûúÂõæË°®ÂÆû‰æãÂ∑≤Â≠òÂú®ÔºåÂÖàÈîÄÊØÅ
    if (chartInstance.value) {
      chartInstance.value.dispose()
      chartInstance.value = null
    }
    // ÈáçÊñ∞ÂàùÂßãÂåñÂõæË°®
    initChart()
  }
}, { deep: true })

onMounted(() => {
  if (props.data && props.data.length > 0) {
    initChart()
  }
})
</script>

<style scoped>
.region-chart {
  background-color: #1b1b4a;
  padding: 10px;
  border-radius: 8px;
  box-shadow: 0 0 6px #0d2451;
  margin-bottom: 12px;
  height: calc(100% - 12px);
  display: flex;
  flex-direction: column;
}
.chart-title {
  color: #99e0ff;
  font-size: 14px;
  font-weight: bold;
  margin-bottom: 10px;
  text-align: center;
  text-shadow: 0 0 10px rgba(110, 221, 241, 0.3);
}
.chart-container {
  flex: 1;
  width: 100%;
  background-color: transparent; /* Á°Æ‰øùËÉåÊôØÈÄèÊòé */
}
</style>